<template>
  <v-app id="inspire">
    <v-navigation-drawer
      v-model="drawer"
      app
    >
      <v-card
        class="mx-auto"
        style="height: 100vh;"
        tile
      >
        <v-list >
          <v-subheader>Станции</v-subheader>
          <v-list-item-group
            v-model="selectedItem"
            color="primary"
          >
            <v-list-item
              v-for="(item, i) in items"
              :key="i"
              @click="goByNav(i)"
            >
              <v-list-item-icon>
                <v-icon v-text="item.icon"></v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title v-text="item.text"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-card>
    </v-navigation-drawer>

    <v-app-bar app>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>

      <v-toolbar-title>Станции</v-toolbar-title>
    </v-app-bar>

    <v-main style="padding-left: 0px !important;">
      <v-data-table
        v-if="selectedItem == 0"
        :headers="headersPlans"
        :items="listPlans"
        :items-per-page="10"
        class="elevation-1 ma-10"
      >
      <template v-slot:[`item.shippingdate`]="{ item }">
        {{ convertedDate(item.shippingdate) }}
      </template>
      <template v-slot:[`item.version`]="{ item }">
        {{ convertedVersion(item.version) }}
      </template>
      <template v-slot:[`item.comment`]="{ item }">
        {{ convertedVersion(item.comment) }}
      </template>


      <template v-slot:[`item.actions`]="{ item }">
        <v-btn icon @click="deleteItem(item)">
          <v-icon color="red">mdi-delete</v-icon>
        </v-btn>
      </template>
      </v-data-table>





      <v-data-table
        v-if="selectedItem == 1"
        :headers="headersStations"
        :items="listStations"
        :items-per-page="10"
        class="elevation-1 ma-10"
      >
      </v-data-table>






      <v-container v-if="selectedItem == 2">
        <v-row align="center">
          <v-col
            class="d-flex"
            cols="12"
            sm="6"
          >
            <v-select
              :items="itemsSelect"
              v-model="selectedPeriod"
              label="Выберите период"
              @click="clickPeriod"
            ></v-select>
          </v-col>
        </v-row>


        <v-form
          ref="form"
          v-if="checkPeriod"
          lazy-validation
        >
          <v-row v-for="(item, index) in reqBody" :key="index">
            <v-col
              cols="1.5"
            >
              <v-text-field
                label="Количество"
                v-model="item.caramount"
                required
              ></v-text-field>
            </v-col>

            <v-col
              cols="1.5"
            >
              <v-select
                :items="listStationsArray"
                v-model="item.fromstationname"
                label="Из станции"
              ></v-select>
            </v-col>

            <v-col
              cols="1.5"
            >
              <v-select
                v-model="item.tostationname"
                :items="listStationsArray"
                label="На станцию"
              ></v-select>
            </v-col>

            <v-col cols="1.5">
              <v-menu
                v-model="item.shippingmenu"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="item.shippingdate"
                    label="Выберите дату"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="item.shippingdate"
                  @input="item.shippingmenu = false"
                ></v-date-picker>
              </v-menu>
            </v-col>


            <v-col cols="1.5">
              <v-text-field
                label="Название товара"
                v-model="item.cargoetsngname"
                required
              ></v-text-field>
            </v-col>



            <v-col cols="1.5">
              <v-text-field
                label="Версия"
                v-model="item.version"
                required
              ></v-text-field>
            </v-col>




            <v-col cols="1.5">
              <v-text-field
                label="Комментарий"
                v-model="item.comment"
                required
              ></v-text-field>
            </v-col>
          </v-row>



          <v-btn @click="saveRes">Сохранить</v-btn>
        </v-form>
      </v-container>



    </v-main>
  </v-app>
</template>
<script>
import moment from 'moment'
export default {
  name: 'Home',
  data(){
    return{
      drawer: true,
      selectedItem: 0,
      selectedPeriod: 0,
      checkPeriod: false, 
      itemsSelect: [
        {text: '1 день', value: 1},
        {text: '1 неделя', value: 2},
        {text: '1 месяц', value: 3}
      ],
      items: [
        { text: 'Все планы', icon: 'mdi-folder-open' },
        { text: 'Все станции', icon: 'mdi-folder-open' },
        { text: 'Добавить план', icon: 'mdi-plus' }
      ],
      headersPlans: [
        { text: 'Количество', value: 'caramount' },
        { text: 'Из станции', value: 'fromstationname' },
        { text: 'На станцию', value: 'tostationname' },
        { text: 'Время', value: 'shippingdate' },
        { text: 'Название товара', value: 'cargoetsngname' },
        { text: 'Версия', value: 'version' },
        { text: 'Комментарий', value: 'comment' },
        { text: 'Действия', value: 'actions' }
      ],
      listPlans: [],
      headersStations: [
        { text: 'Id', value: 'id' },
        { text: 'Станция', value: 'name' },
      ],
      listStations: [],
      reqBody: [],
      listStationsArray: []
    }
  },
  methods: {
    goByNav(item){
      this.selectedItem = item
    },
    convertedDate(item){
      return moment(item).lang("ru").format("DD - MMM YYYY");
    },
    convertedVersion(item){
      if(item) return item;
      return '-'
    },
    clickPeriod(){
      console.log(this.selectedPeriod)
    },
    deleteItem(item){
      const headers = {
          'Access-Control-Allow-Origin':  '*'
      };
      this.$axios.delete(`http://localhost:9001/api/plans/${item.id}`, {headers})
      .then(res => {
        console.log(res)
        if(res) alert("Удалено")
        this.$axios.get("http://localhost:9001/api/plans")
          .then(res => {
            this.listPlans = res.data;
          })
      })
    },
    saveRes(){
      this.$axios.post("http://localhost:9001/api/plans", this.reqBody)
      .then(res => {
        if(res.data.status == "success"){
          alert("Добавлено")
          this.reqBody = []
          this.selectedItem = 0
          this.$axios.get("http://localhost:9001/api/plans")
            .then(res => {
              this.listPlans = res.data;
            })
        } 
      })
    }
  },
  computed: {
  },
  watch: {
    selectedPeriod: function(val){
      if(val == 1){
        this.reqBody = []
        this.reqBody.push({
          caramount: '',
          shippingdate: '',
          shippingmenu: '',
          fromstationname: '',
          tostationname: '',
          cargoetsngname: '',
          version: '',
          comment: '',
        })
        this.checkPeriod = true
      }
      else if (val == 2){
        this.reqBody = []
        this.checkPeriod = true
        for(let i = 0; i < 7; i++){
          this.reqBody.push({
            caramount: '',
            shippingdate: '',
            shippingmenu: '',
            fromstationname: '',
            tostationname: '',
            cargoetsngname: '',
            version: '',
            comment: '',
          })
        }
      }
      else if (val == 3){
        this.reqBody = []
        this.checkPeriod = true
        for(let i = 0; i < 31; i++){
          this.reqBody.push({
            caramount: '',
            shippingdate: '',
            shippingmenu: '',
            fromstationname: '',
            tostationname: '',
            cargoetsngname: '',
            version: '',
            comment: '',
          })
        }
      }
    },
  },
  created(){
    this.$axios.get("http://localhost:9001/api/stations")
    .then(res => {
      this.listStations = res.data;
      this.listStations.map(e => {
        this.listStationsArray.push(e.name)
      });
    })


    this.$axios.get("http://localhost:9001/api/plans")
    .then(res => {
      this.listPlans = res.data;
    })
  }
}
</script>
